.nav {
    max-width: 150px;
    min-width: 150px;
    background-color: #fce3f8;
    display:block;
    height: 105vh;
    margin-top: -45px;
}

nav {
    top: 0;
    padding-top: 5px
}

li {
    padding-left: 5px;
    color: black;
    text-align: center;
    list-style-type: none;
    padding-bottom: 7px;

}

ul {
    margin-top: 5px;
}


a{
    color: black;
}

.bookings {
    background:  url("icons/calendar.svg") no-repeat;
    background-position: 1px;

}

.reviews     {
    background:  url("icons/book.svg") no-repeat;
    background-position: 1px;

}


.home     {
    background:  url("icons/home.svg") no-repeat;
    background-position: 1px;

}

.navLogo {
    width: 150px;
    height: 64px;
    font-weight: 1000;
    line-break: auto;
    color: black;
    padding: 5px 14px 2px;
    font-size: 18px;
    line-height: 21px;
    border-bottom: 4px solid white;
    font-family: serif;
}

